.message {
    display: flex;
    flex-direction: column;
    position: relative;
}

.imageAndText {
    display: flex;
    gap: 10px;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {
    background-color: #F5F7FB;
    border-radius: 10px;
    position: relative;
    padding: 10px;
}

.text:after {
    content: '';
    position: absolute;
    bottom: 0;
    right: calc(100% - 10px);
    border-width: 10px;
    border-style: solid;
    border-color: transparent transparent #F5F7FB transparent;
    transform: translateX(1px);
    z-index: 1;
}


.name {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 19.5px;
}

.time {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 10px;
    line-height: 12.19px;
    position: absolute;
    left: 1%;
    top: 100%;
}

.messageText {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 19.5px;
}
