.button {
    width: 132px;
    height: 30px;
    border-radius: 3px;
    color: white;
    border: none;
    cursor: pointer;
}

/* Default button (blue with white text) */
.default {
    background-color: #0066CC;
    color: white;
}

.default:hover {
    background-color: #005BB5; /* Darker blue on hover */
}

.default:active {
    background-color: #004D99; /* Even darker blue on active */
}

/* Red button (red with white text) */
.red {
    background-color: #CC1439;
    color: white;
}

.red:hover {
    background-color: #B01231; /* Darker red on hover */
}

.red:active {
    background-color: #9F102A; /* Even darker red on active */
}

/* Secondary button (white with blue text and border) */
.secondary {
    border: 1px solid #0066CC;
    background-color: white;
    color: #0066CC;
}

.secondary:hover {
    background-color: #E6F3FF; /* Light blue on hover */
}

.secondary:active {
    background-color: #CCE6FF; /* Darker light blue on active */
}

/* Disabled button state */
.disabled {
    background-color: #004D99; /* Dark blue background */
    color: #002E5C; /* Darker text color */
    cursor: not-allowed; /* Indicate it's disabled */
    opacity: 0.7; /* Make it look dimmer */
}
