.input:focus {
    border-color: #B0B0B0;
    box-shadow: 0 0 5px rgba(127, 191, 255, 0.5);
}

.input {
    width: 372px;
    height: 36px;
    margin: 0;
    box-shadow: none;
    background-color: white;
    padding: 10px;
    color: black;
    border: 1px solid #D1D1D1;

}

.inputWrapper {
    width: 100%;
    padding: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    margin: 0;
}

.superInput {
    border-radius: 5px;
    transition: border-color 0.3s, box-shadow 0.3s;
}

.errorInput {
    border-color: #FF4D4D;
    background-color: #FFE6E6;
}

.errorInput:focus {
    border-color: #FF4D4D;
    box-shadow: 0 0 5px rgba(255, 0, 0, 0.5);
}

.error {
    color: red;
    font-size: 12px;
    margin-top: 5px;
}
